.te-form {
  display: flex;
  flex-direction: column;
  margin: 0 -10px;
}
.te-form-input-group {
  display: flex;
  justify-content: space-between;
}
.te-form-input-group div {
  display: flex;
  flex: 1;
  flex-direction: column;
}
.te-form-error {
  color: #cd5550;
  font-size: 0.9em;
  padding-left: 10px;
}
.te-form input {
  border: 1px solid #a6aaa9;
  flex: 1;
  font-size: inherit;
  padding: 10px 20px;
  margin: 10px;
}
