.te-checkout {
  background-color: #f5f5f5;
  box-sizing: border-box;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 10px;
  position: relative;
  width: 100%;
}
.te-checkout-loader {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 33vh;
}
.te-checkout.desktop {
  padding: 0 18px;
}
.te-checkout * {
  box-sizing: border-box;
}
.te-checkout-heading {
  padding-left: 15px;
  font-size: 1em;
  font-weight: normal;
  margin: 0.5em auto 1em;
  max-width: 1000px;
}
.te-checkout-heading.toggle {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  max-width: 460px;
  width: 100%;
}
.te-checkout-heading.toggle h3 {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}
.te-checkout-heading.toggle svg {
  margin-left: 20px;
  width: 10px;
}
.te-checkout-heading.desktop {
  padding-left: 30px;
}
.te-checkout-outer-wrapper {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
}
.te-checkout-button {
  border-radius: 5px;
  border: 0;
  background-color: #222;
  color: white;
  font-size: 0.76em;
  font-weight: 500;
  height: 40px;
  text-transform: uppercase;
}
.te-checkout-button:disabled {
  background: #a3a0a0;
}
.te-checkout-container {
  background-color: white;
  border-radius: 10px;
  margin: 20px auto 20px;
  max-width: 1000px;
  overflow: hidden;
  padding: 10px 15px;
  position: relative;
  transition: all 0.4s ease;
  width: 100%;
}
.te-checkout-container.desktop {
  padding: 15px 30px;
}
.te-checkout-container.desktop .toggle-edit {
  top: 15px;
  right: 30px;
}
.te-checkout-container.small {
  margin-left: 0;
  max-width: 460px;
}
.te-checkout-container.hide {
  height: 0;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.te-checkout-container .toggle-edit {
  position: absolute;
  top: 10px;
  right: 15px;
}
.te-checkout-container p {
  font-size: 0.8em;
}
.te-checkout-form-outer {
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
.te-checkout-form-outer .te-checkout-form-container {
  height: 40px;
  width: 75%;
}
.te-checkout-form-outer input {
  border: 0;
  height: 100%;
  outline: 0;
  padding: 10px;
  width: 100%;
}
.te-checkout-form-outer button {
  margin-left: 15px;
  width: calc(25% - 15px);
}
.te-checkout-form-container {
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  box-sizing: border-box;
  display: block;
  margin: 5px 0;
  padding: 0;
  position: relative;
  overflow: hidden;
}
.te-checkout-form-container.small {
  display: inline-block;
  height: 50px;
  position: relative;
  width: 50%;
}
.te-checkout-form-container.hide {
  height: 0;
  opacity: 0;
}
.te-checkout-form-input {
  border-bottom: 1px solid #e7e7e7;
  display: block;
  height: 50px;
  position: relative;
}
.te-checkout-form-input.medium {
  border-left: 1px solid #e7e7e7;
  display: inline-block;
  margin-left: -1px;
  width: 50%;
}
.te-checkout-form-input.small {
  border-left: 1px solid #e7e7e7;
  display: inline-block;
  margin-left: -1px;
  width: 25%;
}
.te-checkout-form-input input {
  border: 0;
  font-size: 0.9em;
  height: 100%;
  outline: 0;
  padding: 20px 10px 10px;
  width: 100%;
}
.te-checkout-form-input input::-moz-placeholder,
.te-checkout-form-input input:-ms-input-placeholder,
.te-checkout-form-input input:-moz-placeholder {
  color: #a3a0a0;
}
.te-checkout-form-input input::placeholder,
.te-checkout-form-input input::-webkit-input-placeholder {
  color: #a3a0a0;
}
.te-checkout-form-input input:focus::placeholder,
.te-checkout-form-input input:not(:placeholder-shown)::placeholder {
  opacity: 0;
}
.te-checkout-form-input input:focus + .te-checkout-form-input-label,
.te-checkout-form-input input:not(:placeholder-shown) + .te-checkout-form-input-label {
  opacity: 1;
  transform: translateY(-12px);
}
.te-checkout-form-input label {
  height: 100%;
  position: relative;
}
.te-checkout-form-input-label {
  color: #a3a0a0;
  font-size: 0.7em;
  left: 10px;
  opacity: 0;
  position: absolute;
  top: 18px;
  transition: transform 0.3s ease-in;
}
.te-checkout-form-error {
  color: #cd5550;
  font-size: 0.8em;
  left: 10px;
  position: absolute;
  top: 2px;
}
.te-checkout-form-error + .te-checkout-form-input-label {
  opacity: 0;
}
.te-checkout-tooltip {
  align-items: center;
  background-color: #a3a0a0;
  border-radius: 50%;
  color: white;
  display: flex;
  font-size: 0.8em;
  font-weight: 600;
  height: 18px;
  justify-content: center;
  width: 18px;
}
.te-checkout-address-summary {
  padding: 10px;
}
.te-checkout-payment-summary {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.te-checkout-payment-summary-control-row {
  display: flex;
  margin-bottom: 5px;
}
.te-checkout-payment-summary-toggle-link {
  cursor: pointer;
  text-decoration: underline;
}
.te-checkout-payment-summary-total-price {
  font-weight: bold;
}
.te-checkout-cart-summary-rows-container {
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  padding: 10px;
  font-size: 0.8em;
}
.te-checkout-cart-summary-row {
  display: flex;
  justify-content: space-between;
}
.te-checkout-cart-summary-separator {
  height: 1px;
  border-top: 1px solid #e7e7e7;
  margin: 10px 0;
}
.te-checkout-stripe-input {
  border-bottom: 1px solid #e7e7e7;
  padding: 15px 10px;
}
.te-checkout-stripe-input.small {
  border-bottom: 0;
  border-left: 1px solid #e7e7e7;
  display: inline-block;
  margin-left: -1px;
  width: 50%;
}
.te-checkout-stripe-input:last-of-type {
  border: 0;
}
.te-checkout-payment-view {
  display: flex;
  flex-direction: column;
}
.te-checkout .te-button {
  border-radius: 5px;
  display: block;
  line-height: 2.7em;
  margin: 0 auto;
  max-width: 305px;
  overflow: hidden;
}
.te-checkout-tooltip {
  left: calc(50% + 10px);
  position: absolute;
  bottom: 25px;
  transform: translateY(-50%);
}
.te-checkout-tooltip.destop {
  left: calc(100% + 10px);
  top: 50%;
}
.te-checkout-payment-view {
  display: flex;
  flex-direction: column;
}
.te-checkout-payment-toggle {
  display: flex;
  font-size: 0.9em;
  justify-content: center;
  padding: 15px 0;
}
.te-checkout-payment-toggle span {
  padding-right: 10px;
}
.te-checkout-payment-toggle button {
  text-decoration: underline;
}
.te-checkout-payment-total {
  display: flex;
  font-weight: 500;
  justify-content: center;
  margin-bottom: 15px;
}
.te-checkout-container .__react_component_tooltip {
  max-width: 300px !important;
}
.te-form-input-group .te-stripe-container:first-child {
  margin-right: 20px;
}
