.te-modal-description-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f6f6f6;
  margin: 0 20px 80px 20px;
}
.te-modal-description-title {
  margin: 30px 0 10px 0;
  font-weight: 500;
  font-size: 16px;
  color: #4f5058;
}
.te-modal-description-content {
  padding: 10px 23px 30px 23px;
  word-break: break-word;
  margin: 0 auto;
  font-size: 14px;
  max-width: 900px;
}
.te-modal-description-content p {
  font-size: 14px;
}
.te-modal-description-container.large .te-modal-description-title {
  margin: 50px 0 10px 0;
}
.te-modal-description-container.large .te-modal-description-content {
  padding: 10px 23px 50px 23px;
}
