.te-checkout-heading.toggle {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  max-width: 460px;
  width: 100%;
}
.te-checkout-heading.toggle h3 {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}
.te-checkout-heading.toggle svg {
  margin-left: 20px;
  width: 10px;
  transform: rotate(180deg);
}
.te-checkout-heading.toggle svg.open {
  transform: rotate(0deg);
}
.te-checkout-heading-tick-icon-container {
  width: 24px;
  height: 24px;
  position: relative;
  top: 4px;
  right: 32px;
}
