.te-footer {
  box-sizing: border-box;
  font-family: inherit;
  font-size: 14px;
  text-align: center;
  border-top: 1px solid #dcdee0;
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}
.te-footer-contact {
  padding: 20px 25px;
  display: flex;
  color: #4f5058;
  flex-direction: column;
  align-items: flex-start;
}
.te-footer-contact.large {
  flex-direction: row;
  justify-content: space-around;
}
.te-footer-contact-title {
  font-size: 12px;
  font-weight: 500;
  text-align: left;
}
.te-footer-contact-shopping {
  text-align: left;
  margin-top: 10px;
}
.te-footer-contact-help {
  text-align: left;
  margin-top: 10px;
}
.te-footer-contact-email {
  text-align: right;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 10px;
}
.te-footer-contact-list {
  padding: 0;
  display: flex;
  flex-direction: column;
  line-height: 1.6em;
  font-size: 11px;
  list-style-type: none;
}
.te-footer-contact-list.large {
  font-size: 12px;
}
.te-footer-contact-list-item {
  display: flex;
  align-items: center;
  line-height: 2.5em;
}
.te-footer-contact-list-item svg {
  width: 20px;
  height: 20px;
  margin-right: 7px;
}
.te-footer-contact-list-link {
  line-height: 2.4em;
}
.te-footer-payment {
  display: flex;
  flex-direction: column;
  text-align: left;
}
@media (min-width: 768px) {
  .te-footer-payment {
    margin: 20px auto;
    text-align: center;
  }
}
.te-footer-payment-title {
  font-weight: 500;
  margin: 30px auto 10px auto;
  font-size: 12px;
  letter-spacing: 0.5px;
}
.te-footer-payment-logos {
  display: flex;
  justify-content: center;
  align-items: center;
}
.te-footer-payment-logos :not(:first-child):not(:last-child) {
  margin: 0 10px;
}
.te-footer-payment-logo {
  border-radius: 5px;
  width: 45px;
  height: 35px;
  padding: 10px 0;
  padding: 10px 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.te-footer-payment-logo > svg {
  width: 100%;
  height: 100%;
}
.te-footer-tipser-logo {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  margin: 5px 30px 40px 0;
  color: #4f5058;
}
.te-footer-tipser-logo-icon {
  margin-left: 7px;
}
.te-footer-tipser-logo-icon svg {
  width: 100px;
}
.te-footer-terms {
  padding: 0 25px;
  width: fit-content;
}
.te-footer-terms.large {
  margin: 0 auto;
}
.te-footer-terms-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0 30px 0;
  padding: 0;
  font-size: 12px;
}
.te-footer-terms-single {
  margin-bottom: 10px;
}
.te-footer .footer-link a {
  color: #262626;
  text-decoration: none;
}
.te-footer .footer-link a:hover {
  color: #a3a0a0;
}
.te-footer-contact .te-footer-contact-list-link a,
.te-footer-terms .te-footer-contact-list-link a {
  color: #262626;
  text-decoration: none;
}
.te-footer-contact .te-footer-contact-list-link a:hover,
.te-footer-terms .te-footer-contact-list-link a:hover {
  color: #a3a0a0;
}
.te-footer-contact .te-footer-terms-single a,
.te-footer-terms .te-footer-terms-single a {
  color: #262626;
  text-decoration: none;
}
.te-footer-contact .te-footer-terms-single a:hover,
.te-footer-terms .te-footer-terms-single a:hover {
  color: #a3a0a0;
}
.te-footer-contact .te-footer-contact-list-item a,
.te-footer-terms .te-footer-contact-list-item a {
  color: #262626;
  text-decoration: none;
}
.te-footer-contact .te-footer-contact-list-item a:hover,
.te-footer-terms .te-footer-contact-list-item a:hover {
  color: #a3a0a0;
}
