.te-checkout {
  background-color: #f5f5f5;
  box-sizing: border-box;
  margin: 0 auto;
  overflow-x: hidden;
  padding: 10px;
  position: relative;
  width: 100%;
}
.te-checkout-loader {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 33vh;
}
.te-checkout.desktop {
  padding: 0 18px;
}
.te-checkout * {
  box-sizing: border-box;
}
.te-checkout-heading {
  padding-left: 15px;
  font-size: 1em;
  font-weight: normal;
  margin: 0.5em auto 1em;
  max-width: 1000px;
}
.te-checkout-heading.toggle {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  margin-left: 0;
  max-width: 460px;
  width: 100%;
}
.te-checkout-heading.toggle h3 {
  font-size: 1em;
  font-weight: normal;
  margin: 0;
}
.te-checkout-heading.toggle svg {
  margin-left: 20px;
  width: 10px;
}
.te-checkout-heading.desktop {
  padding-left: 30px;
}
.te-checkout-outer-wrapper {
  margin: 0 auto;
  max-width: 1000px;
  width: 100%;
}
.te-checkout-button {
  border-radius: 5px;
  border: 0;
  background-color: #222;
  color: white;
  font-size: 0.76em;
  font-weight: 500;
  height: 40px;
  text-transform: uppercase;
}
.te-checkout-button:disabled {
  background: #a3a0a0;
}
.te-checkout-container {
  background-color: white;
  border-radius: 10px;
  margin: 20px auto 20px;
  max-width: 1000px;
  overflow: hidden;
  padding: 10px 15px;
  position: relative;
  transition: all 0.4s ease;
  width: 100%;
}
.te-checkout-container.desktop {
  padding: 15px 30px;
}
.te-checkout-container.desktop .toggle-edit {
  top: 15px;
  right: 30px;
}
.te-checkout-container.small {
  margin-left: 0;
  max-width: 460px;
}
.te-checkout-container.hide {
  height: 0;
  margin-top: 0;
  margin-bottom: 0;
  opacity: 0;
  padding-top: 0;
  padding-bottom: 0;
}
.te-checkout-container .toggle-edit {
  position: absolute;
  top: 10px;
  right: 15px;
}
.te-checkout-container p {
  font-size: 0.8em;
}
.te-checkout-form-outer {
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
.te-checkout-form-outer .te-checkout-form-container {
  height: 40px;
  width: 75%;
}
.te-checkout-form-outer input {
  border: 0;
  height: 100%;
  outline: 0;
  padding: 10px;
  width: 100%;
}
.te-checkout-form-outer button {
  margin-left: 15px;
  width: calc(25% - 15px);
}
.te-checkout-form-container {
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  box-sizing: border-box;
  display: block;
  margin: 5px 0;
  padding: 0;
  position: relative;
  overflow: hidden;
}
.te-checkout-form-container.small {
  display: inline-block;
  height: 50px;
  position: relative;
  width: 50%;
}
.te-checkout-form-container.hide {
  height: 0;
  opacity: 0;
}
.te-checkout-form-input {
  border-bottom: 1px solid #e7e7e7;
  display: block;
  height: 50px;
  position: relative;
}
.te-checkout-form-input.medium {
  border-left: 1px solid #e7e7e7;
  display: inline-block;
  margin-left: -1px;
  width: 50%;
}
.te-checkout-form-input.small {
  border-left: 1px solid #e7e7e7;
  display: inline-block;
  margin-left: -1px;
  width: 25%;
}
.te-checkout-form-input input {
  border: 0;
  font-size: 0.9em;
  height: 100%;
  outline: 0;
  padding: 20px 10px 10px;
  width: 100%;
}
.te-checkout-form-input input::-moz-placeholder,
.te-checkout-form-input input:-ms-input-placeholder,
.te-checkout-form-input input:-moz-placeholder {
  color: #a3a0a0;
}
.te-checkout-form-input input::placeholder,
.te-checkout-form-input input::-webkit-input-placeholder {
  color: #a3a0a0;
}
.te-checkout-form-input input:focus::placeholder,
.te-checkout-form-input input:not(:placeholder-shown)::placeholder {
  opacity: 0;
}
.te-checkout-form-input input:focus + .te-checkout-form-input-label,
.te-checkout-form-input input:not(:placeholder-shown) + .te-checkout-form-input-label {
  opacity: 1;
  transform: translateY(-12px);
}
.te-checkout-form-input label {
  height: 100%;
  position: relative;
}
.te-checkout-form-input-label {
  color: #a3a0a0;
  font-size: 0.7em;
  left: 10px;
  opacity: 0;
  position: absolute;
  top: 18px;
  transition: transform 0.3s ease-in;
}
.te-checkout-form-error {
  color: #cd5550;
  font-size: 0.8em;
  left: 10px;
  position: absolute;
  top: 2px;
}
.te-checkout-form-error + .te-checkout-form-input-label {
  opacity: 0;
}
.te-checkout-tooltip {
  align-items: center;
  background-color: #a3a0a0;
  border-radius: 50%;
  color: white;
  display: flex;
  font-size: 0.8em;
  font-weight: 600;
  height: 18px;
  justify-content: center;
  width: 18px;
}
.te-checkout-address-summary {
  padding: 10px;
}
.te-checkout-product-list {
  background-color: #ffffff;
  border-bottom: 1px solid #e7e7e7;
}
.te-checkout-product-list:last-child {
  border: 0;
}
.te-checkout-product-list > .te-button-clear {
  width: 100%;
  display: block;
}
.te-checkout-product-merchant {
  color: #a3a0a0;
  font-size: 0.7em;
  height: auto;
  overflow: hidden;
  position: relative;
  transition: all 0.5s ease-in-out;
}
.te-checkout-product-merchant-row {
  display: flex;
  line-height: 1.3;
  padding: 5px 0;
}
.te-checkout-product-merchant-row:first-child .label {
  width: 95px;
}
.te-checkout-product-merchant-row:first-child .value {
  color: #a3a0a0;
}
.te-checkout-product-merchant-row:last-child {
  padding-bottom: 15px;
}
.te-checkout-product-merchant .label {
  color: #4f5058;
}
.te-checkout-product-merchant .uppercase {
  text-transform: uppercase;
}
.te-checkout-product-merchant .value {
  color: #4f5058;
  padding-left: 10px;
}
.te-checkout-product-merchant svg {
  fill: currentColor;
  height: 10px;
  position: absolute;
  right: 0;
  transform: rotate(180deg);
  width: 10px;
}
.te-checkout-product-merchant.closed {
  height: 2.5em;
}
.te-checkout-product-merchant.closed svg {
  transform: rotate(0deg);
}
.te-checkout-product-item {
  border-top: 1px solid #e7e7e7;
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  height: 180px;
  padding: 10px 0 0;
}
.te-checkout-product-item-wrapper {
  display: flex;
}
.te-checkout-product-item-summary {
  align-items: center;
  display: flex;
  flex: 1;
  justify-content: flex-end;
}
.te-checkout-product-image {
  background-color: transparent;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
  height: 130px;
  margin-right: 15px;
  position: relative;
  width: 90px;
}
.te-checkout-product-image:before {
  position: absolute;
  background: #000;
  opacity: 0.05;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  content: '';
}
.te-checkout-product-info {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding-right: 35px;
}
.te-checkout-product-remove {
  position: absolute;
  right: 0;
  top: 10px;
}
.te-checkout-product-remove svg {
  width: 10px;
  font-size: 10px;
  height: 10px;
}
.te-checkout-product-brand,
.te-checkout-product-name {
  font-weight: 500;
  line-height: 1.3;
  text-transform: uppercase;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.te-checkout-product-brand {
  color: #a3a0a0;
}
.te-checkout-product-name {
  color: #4f5058;
}
.te-checkout-product-price {
  line-height: 1.3em;
}
.te-checkout-product-price-sales .regular {
  color: #ec877b;
  text-decoration: line-through;
}
.te-checkout-product-attribute {
  display: flex;
  flex-wrap: wrap;
}
.te-checkout-product-attribute span {
  display: block;
  padding: 0 10px 2px 0;
  max-width: 90px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.te-checkout-product-quantity-select {
  border: 1px solid #e7e7e7;
  font-size: 0.9em;
  height: 30px;
  margin: 5px 0 0 -1px;
  position: relative;
  width: 85px;
}
.te-checkout-product-quantity-select select {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: transparent;
  border: 0;
  height: 30px;
  outline: none;
  padding: 5px 15px;
  width: 100%;
}
.te-checkout-product-quantity-select svg {
  fill: currentColor;
  height: 10px;
  pointer-events: none;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
}
.te-checkout-product-quantity-text {
  border: 1px solid #e7e7e7;
  font-size: 0.9em;
  height: 30px;
  margin: 5px 0 0 -1px;
  position: relative;
  width: 85px;
}
.te-checkout-product-quantity-text input {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: transparent;
  border: 0;
  height: 29px;
  outline: none;
  padding: 5px 15px;
  width: 100%;
}
.te-checkout-product-quantity-text input::-webkit-outer-spin-button,
.te-checkout-product-quantity-text input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.te-checkout-product-line-wrapper {
  display: flex;
  align-items: flex-end;
}
.te-checkout-product-slide {
  background-color: white;
  height: 100vh;
  left: 100%;
  overflow: scroll;
  position: fixed;
  top: 0;
  transition: all 1s ease-in-out;
  width: 100vw;
  z-index: 99;
}
.te-checkout-product-slide.open {
  left: 0;
}
.te-checkout-product-slide.closed {
  left: 100%;
}
.te-checkout-product-slide-back {
  align-items: center;
  background-color: white;
  cursor: pointer;
  display: flex;
  font-size: 0.9em;
  padding: 10px;
}
.te-checkout-product-slide-back svg {
  height: 15px;
  padding: 0 10px;
}
.te-checkout-error {
  color: #ec877b;
  font-size: 0.8em;
  padding-left: 10px;
}
